html{
    font-size: 62.5%;
    box-sizing: border-box;
}

* , *:before ,*:after {
    box-sizing: inherit;
}

body{
    font-family: $mainFont;
    font-size: 1.6rem;
    &.fix-body{
        overflow: hidden;
    }
}

p{
    font-size: 2rem;
    color: $Black;
}

.container{
    max-width: 120rem;
    width: 95%;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

img{
    max-width: 100%;
}

h1 , h2 , h3 {
    margin: 0;
}

h1{
    font-size: 4rem;
    font-weight: $blacked;
}

h2{
    font-size: 3.6rem;
}

h3{
    font-size: 3rem;
}

