@mixin telf{
    @media(min-width: $telf){
        @content;
    }
}

@mixin tablet{
    @media(min-width: $tablet){
        @content;
    }
}

@mixin desktop{
    @media(min-width: $desktop){
        @content;
    }
}

@mixin grid($cols,$space){
    display: grid;
    grid-template-columns: repeat($cols,1fr);
    gap: $space;
}
